html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

.icon-arrow_down {
  width: 1em;
  height: 1em;
  fill: initial;
}

.icon-atomic {
  width: 1em;
  height: 1em;
  fill: initial;
}

.icon-board {
  width: 1em;
  height: 1em;
  fill: initial;
}

.icon-chat {
  width: 1em;
  height: 1em;
  fill: initial;
}

.icon-dots {
  width: 4.5em;
  height: 1em;
  fill: initial;
}

.icon-gear {
  width: 1em;
  height: 1em;
  fill: initial;
}

.icon-hamburger {
  width: 1em;
  height: 1em;
  fill: initial;
}

.icon-refresh {
  width: 1em;
  height: 1em;
  fill: initial;
}

@font-face {
  font-family: overpass;
  src: url("../fonts/overpass-regular.woff2") format("woff2"), url("../fonts/overpass-regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

body {
  background-color: #f1f1f1;
  line-height: 1.4;
  font-family: overpass, sans-serif;
  font-size: 16px;
}

.navigation {
  width: 130px;
  height: 100%;
  flex-shrink: 0;
  background-image: url(/img/navigation_bg.jpg);
  background-repeat: repeat-y;
}

.navigation__list {
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
}

.navigation__item:after {
  content: "";
  display: inline-block;
  width: 30px;
  height: 5px;
  background-image: url(/icons/dots.svg);
  background-repeat: no-repeat;
  background-position: center center;
  background-size: contain;
  fill: red;
}

.navigation__item:last-child:after, .navigation__link--settings:after {
  display: none;
}

.navigation__link {
  position: relative;
  display: block;
  padding: 30px 20px;
  text-decoration: none;
}

.navigation__link:hover {
  background-color: rgba(255, 255, 255, 0.08);
  transition: background-color .3s ease-in;
  text-decoration: underline;
  color: #fff;
}

.navigation__link:hover .navigation__text, .navigation__link--active .navigation__text, .navigation__link:hover .icon {
  color: inherit;
  fill: #fff;
}

.navigation__icon .icon {
  display: block;
  width: 45px;
  height: 45px;
  margin: 0 auto;
  fill: rgba(103, 103, 103, 0.6);
}

.navigation__text {
  color: rgba(103, 103, 103, 0.6);
}

.navigation__link--active {
  position: relative;
  color: #fff;
}

.navigation__link--active::before {
  content: '';
  display: block;
  height: 110px;
  width: 4px;
  background-color: #ff503f;
  position: absolute;
  left: 0;
  top: 12px;
  border-radius: 0 12px 12px 0;
}

.navigation__link--active .icon {
  fill: #ff503f;
  width: 45px;
  height: 45px;
}

.navigation__counter {
  position: absolute;
  top: 20px;
  right: 20px;
  width: 32px;
  height: 32px;
  font-size: 16px;
  line-height: 2;
  border-radius: 50%;
  text-decoration: none;
  color: #fff;
  text-align: center;
  background-color: #adadad;
}

.navigation__counter--urgent {
  background-color: #ff503f;
}

.navigation__user {
  border-radius: 50%;
  overflow: hidden;
}

.navigation__user--avatar {
  display: block;
  width: 100%;
  height: auto;
}

.logout {
  display: inline-block;
  width: 45px;
  height: 45px;
  border: 7px solid #ff503f;
  border-radius: 50%;
  position: relative;
}

.logout::after {
  content: '';
  position: absolute;
  top: 50%;
  left: 13px;
  transform: translateX(-50%);
  transform: translateY(-50%);
  display: block;
  width: 5px;
  height: 20px;
  background-color: #ff503f;
}

.dashboard__top {
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 30px;
}

.refresh {
  background-color: #ff503f;
}

.refresh__button {
  width: inherit;
  height: inherit;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #ff503f;
  border-radius: inherit;
  transform: scaleX(-1);
}

.refresh__button:hover {
  box-shadow: 0 0 5px 5px rgba(103, 103, 103, 0.6);
  transition: box-shadow .2s ease-in;
}

.icon-refresh {
  display: block;
  fill: #fff;
  width: 16px;
  height: 16px;
}

.button {
  display: inline-block;
  margin-right: 10px;
  height: 40px;
  width: 40px;
  border: none;
  border-radius: 50%;
}

.breadcrumbs {
  display: inline-block;
}

.beadcrumbs__item {
  display: inline-block;
  height: 40px;
  border-radius: 20px;
  padding: 9px 20px;
  border: 1px solid #c8c8c8;
  color: #4B4B4B;
}

.beadcrumbs__item:hover {
  cursor: pointer;
  border-color: #ff503f;
  color: #ff503f;
  transition: color .3s ease-in;
}

.beadcrumbs__item:active {
  background-color: #777;
}

.breadcrumbs__link {
  text-decoration: none;
  font-size: 16px;
  color: inherit;
}

.status {
  margin-left: auto;
  display: flex;
  flex-direction: column;
  text-align: right;
}

.status__button {
  margin-bottom: 10px;
  margin-left: auto;
  background-color: #aacc4e;
  font-size: 20px;
  fill: #fff;
  padding-top: 8px;
  text-align: center;
  order: -1;
  border: 1px solid transparent;
}

.status__button .icon-arrow_down {
  fill: #fff;
}

.status__button:hover {
  box-shadow: 0 0 5px 5px rgba(103, 103, 103, 0.6);
  transition: box-shadow .2s ease-in;
}

.status__label {
  display: block;
  padding: 5px 19px 5px 5px;
  cursor: pointer;
  border-radius: 13px;
}

.status__label:hover {
  background-color: rgba(119, 119, 119, 0.2);
}

.status__label::after {
  content: "";
  position: relative;
  top: 3px;
  width: 20px;
  height: 20px;
  display: inline-block;
  border-radius: 50%;
  border: 1px solid transparent;
}

.status__label--online::after {
  background-color: #aacc4e;
}

.status__label--disturb::after {
  background-color: #ff503f;
}

.status__label--invisible::after {
  background-color: transparent;
  border-color: #777;
}

.status__text {
  word-wrap: normal;
}

.status__online:checked ~ .status__button {
  background-color: #aacc4e;
}

.status__dont-disturb:checked ~ .status__button {
  background-color: #ff503f;
}

.status__invisible:checked ~ .status__button {
  background-color: transparent;
  border-color: #777;
}

.online__button {
  background-color: transparent;
  border: 1px solid #c8c8c8;
  border-radius: 20px;
  height: 40px;
  text-transform: uppercase;
  font-size: 16px;
  line-height: 40px;
  padding-left: 10px;
  padding-right: 10px;
}

.online__button .icon {
  font-size: 24px;
  position: relative;
  top: 5px;
}

.online__list {
  display: none;
}

.online__list--active {
  display: block;
}

.online__item {
  padding: 5px 15px;
}

.activities {
  max-width: 390px;
  height: 100%;
  overflow: hidden;
  box-shadow: -3px 5px 7px 0 #000;
}

.activities__list {
  height: 100%;
  width: 100%;
  border: 1px solid rgba(103, 103, 103, 0.6);
  border-radius: 3px;
}

.activities__list {
  margin: 0;
  display: block;
  padding: 25px;
}

.user__avatar {
  display: inline-block;
}

.user__activities {
  display: inline-block;
}

.user__action {
  display: block;
}

.dashboard {
  width: 100%;
}

.main-wrapper {
  display: flex;
  justify-content: space-between;
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  clip: rect(0 0 0 0);
  height: 1px;
  width: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
}
